import React from "react";
import GalkaBlue from "../assets/img/galka-blue.png"
import GalkaRed from "../assets/img/galka-red.png"
import Arrow1 from "../assets/img/arrow-1.png"
import '../styles/ShowHow.css'; //  

const ShowHow = () => {
    return (
        <div className="showhow">
            <h1>  </h1>
            <div className="heading-showhow-lower">
                <h1> , :</h1>
            </div>
            <div className="cable">
                <img src={GalkaBlue} alt='' />
                <span className="cable-1">      </span>
            </div>
            <div className="arrow-1"><img src={Arrow1} alt='' /></div>
            <div className="equipment">
                <span className="galka-red"><img src={GalkaRed} alt='' /></span>
                <span className="equipment-1">    </span>
            </div>
            <div className="arrow-2"><img src={Arrow1} alt='' /></div>
            <div className="ref">
                <span className="galka-blue"><img src={GalkaBlue} alt='' /></span>
                <span className="ref-1">       </span>
            </div>
            <div className="arrow-3"><img src={Arrow1} alt='' /></div>
            <div className="certificate">
                <span className="galka-red"><img src={GalkaRed} alt='' /></span>
                <span className="certificate-1">    </span>
            </div>
        </div>
    )
}

export default ShowHow